  getBtcRate(){
    return new Promise((resolve, reject) => {
       var apilink = "https://chain.so/api/v2/get_price/BTC/USD"
      this.http.get(apilink )
//	.map(res=> res.json())
        .subscribe((res: any) => {
          if(res != null && res.data != null)
          this.btcRate = res.data.prices[0].price;
          this.storage.set("bitcoinrate", res);
          resolve(res);
        }, (err) => {
          this.storage.get("bitcoinrate").then(res=>{
          if(res != null && res.data != null)
          this.btcRate = res.data.prices[0].price;
          resolve(res);
          });
        });

    });
  }

  getCurrencies(){

  var data = {
 	 "id": "test",
  	"jsonrpc": "2.0",
  	"method": "getCurrencies",
  	"params": {
  	}
  	} ;
    return new Promise((resolve, reject) => {

      let headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('api-key', this.apiKey);
      headers.append('sign', this.getsign(data) );

      this.http.post(this.url ,JSON.stringify(data), {headers: headers})
//        .map(res => res.json())
        .subscribe((res: any) => {
          resolve(res._body);
          // resolve(res);
        }, (err) => {
          reject(err);
        });

    });

  }

