import { Component, OnInit, ViewChild } from '@angular/core';
import { NavParams, ModalController } from '@ionic/angular';
import {NgxQRCodeComponent} from 'ngx-qrcode2';



@Component({
  selector: 'app-printdisplaymodal',
  templateUrl: './printdisplaymodal.page.html',
  styleUrls: ['./printdisplaymodal.page.scss'],
})
export class PrintdisplaymodalPage implements OnInit {
//  @ViewChild (NgxQRCodeComponent, {static: false}) myqr: any;
 myqr: any;

  elementType = "dummy";
  item;

  constructor(private params: NavParams, 
    private modalctrl: ModalController) {
    this.item = params.get('obj');
  }




  ngOnInit() {
  }

  dismiss() {
    this.modalctrl.dismiss();
  }

   print(): void {
    let printContents, popupWin;
    printContents = document.getElementById('print-section').innerHTML;
    popupWin = window.open('', '_blank', 'top=0,left=0,height=100%,width=auto');
    popupWin.document.open();
    popupWin.document.write(`
      <html>
        <head>
          <title>Print tab</title>
          <style>
          //........Customized style.......
          </style>
        </head>
    <body onload="window.print();window.close()">${printContents}</body>
      </html>`
    );
    popupWin.document.close();
}

}
