import { Router } from '@angular/router';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Events, MenuController, Platform } from '@ionic/angular';
import { Storage } from '@ionic/storage';
import { AlertController } from '@ionic/angular';
import { AngularFireAuth } from '@angular/fire/auth';

import { TermsuserService } from './pages/termspage/terms-user.service';

import { UserData } from './providers/user-data';

import {environment} from './pages/config/environment';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AppComponent implements OnInit {
  appPages = [
    {
      title: 'Payment',
      url: '/tabs/dashaccept',
      control: 'false',
      icon: 'calendar'
    },
    {
      title: 'Group note',
      url: '/npnote',
      control: 'false',
      icon: 'log-in'
    },
    {
      title: 'Agreement',
      url: '/2pcontract',
      control: 'false',
      icon: 'log-in'
    },
    {
      title: 'Document',
      url: '/docvalidate',
      control: 'true',
      icon: 'document'
    },
    {
      title: 'Payment ',
      url: '/1pnote',
      control: 'false',
      icon: 'arrow-round-forward'
    },
    {
      title: 'Escrow',
      url: '/2ptescrow',
      control: 'false',
      icon: 'arrow-round-forward'
    },
    {
      title: 'Sign document',
      url: '/documentsign',
      control: 'false',
      icon: 'log-in'
    },
/*
    {
      title: 'Templates',
      url: '/invoicetemplate',
      icon: 'log-in'
    },
*/
    {
      title: 'Misc',
      url: '/wallet',
      control: 'true',
      icon: 'wallet'
    },
/*
    {
      title: 'Operation',
      url: '/operation',
      icon: 'wallet'
    },
    {
      title: 'About',
      url: '/tabs/about',
      icon: 'information-circle'
    },
    {
      title: 'Deposit',
      url: '/tabs/deposit',
      icon: 'information-circle'
    }
*/
  ];
  companyappPages = [
  ];

  loggedIn = false;
   
  useraccount : any = {
     termsagreed: 'no',
     dashprice: '0'
  } ;


  constructor(
    private events: Events,
    private menu: MenuController,
     private fireauth: AngularFireAuth,

    private platform: Platform,
     public termsuserservice: TermsuserService,
     public alertController: AlertController,

    private router: Router,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private storage: Storage,
    private userData: UserData
  ) {
    this.initializeApp();
    // this.router.navigateByUrl('/tabs/dashaccept');
  }

  ngOnInit() {
    this.checkLoginStatus();
    this.listenForLoginEvents();
  }

  ionViewWillEnter() {
  this.loadaccount() ;
  }

  willbeavailable() {
    this.presentAlert("Will be available shortly", "Contact below email to subscribe.", "Email-id: support@thepaybox.biz. ");

  }


  async  loadaccount() {

   this.useraccount = await this.termsuserservice.getuseraccount();

 }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  checkLoginStatus() {
    return this.userData.isLoggedIn().then(loggedIn => {
      return this.updateLoggedInStatus(loggedIn);
    });
  }

  updateLoggedInStatus(loggedIn: boolean) {
    setTimeout(() => {
      this.loggedIn = loggedIn;
    }, 300);
  }

  listenForLoginEvents() {
    this.events.subscribe('user:login', () => {
      this.updateLoggedInStatus(true);
    });

    this.events.subscribe('user:signup', () => {
      this.updateLoggedInStatus(true);
    });

    this.events.subscribe('user:logout', () => {
      this.updateLoggedInStatus(false);
    });
  }


  openTutorial() {
    this.menu.enable(false);
    this.storage.set('ion_did_tutorial', false);
    this.router.navigateByUrl('/tutorial');
  }
   async presentAlert(title, subheader, message) {
    const alert = await this.alertController.create({
      header: title,
      subHeader: subheader,
      message: message,
      buttons: ['OK']
    });

    await alert.present();
    }
   
 logoutuser() {
       this.storage.set(environment.storageuniq+'loggedinuser',null).then(x=> {
        //this.loggedinuser = null;
       });;

    this.storage.set(environment.storageuniq+'useraccount', null).then(xx=>{
       this.termsuserservice.reflectuseraccount();

   });;

  }


   logout() {
    this.logoutuser() ;
    this.fireauth.auth.signOut().then(() => {
      this.router.navigate(['/tabs/login']);
    })
  }


}
